import React from "react";
import { Link } from "react-router-dom";

import "./indice.scss";

const IndiceItem = props => {
  const items = props.items.map((item, key) => {
    return (
      <div className="section_item" key={key}>
        {props.name !== "Comunidade central" && (
          <Link to={`${item.link}`}>- {item.label}</Link>
        )}
        {props.name === "Comunidade central" && (
          <a href={item.link} target="_blank">
            - {item.label}
          </a>
        )}
      </div>
    );
  });

  return (
    <>
      <div className="section_title">{props.name}</div>

      <div className="section_list">{items}</div>
    </>
  );
};

const IndiceSections = props => {
  const items = props.sections.map((item, key) => {
    return <IndiceItem {...item} key={key} />;
  });

  return <div className="indice_sections">{items}</div>;
};

const Indice = props => {
  let index = require("./indice.jsx");
  let sections = index.default;

  return (
    <div className="indice">
      <IndiceSections {...sections} />
    </div>
  );
};

export default Indice;
