import React from "react";

import "./pokemons.scss";

const PokemonItem = props => {
  let pokemon = props.pokemon;

  let tipos = pokemon.tipos.map((item, key) => {
    return (
      <img
        src={`https://www.serebii.net/pokedex-bw/type/${item}.gif`}
        alt={item}
        key={key}
      />
    );
  });

  let moveset = pokemon.moveset.map((item, key) => {
    return <div key={key}>- {item}</div>;
  });

  return (
    <div className="pokemon_item">
      <div className="pokemon_title">
        <div>
          <div>{pokemon.apelido}</div>
          <div>LVL {pokemon.level}</div>
        </div>

        <div>
          <div>{pokemon.especie}</div>
          <div>
            {pokemon.genero === "male"
              ? "♂"
              : pokemon.genero === "female"
              ? "♀"
              : "∅"}{" "}
            {pokemon.shiny && <span>★</span>}
          </div>
        </div>
      </div>

      <div className="pokemon_body">
        <div className="pokemon_picture">
          <img src={pokemon.foto} alt={pokemon.especie} />
        </div>

        <div className="pokemon_info">
          <div className="pokemon_types">{tipos}</div>

          <div>
            <b>HABILIDADE:</b> {pokemon.habilidade}
          </div>
          <div>
            <b>NATUREZA:</b> {pokemon.natureza}
          </div>

          <div className="pokemon_moveset">
            <div>
              <b>MOVESET:</b>
            </div>
            {moveset}
          </div>
        </div>
      </div>
    </div>
  );
};

const Pokemons = props => {
  let items = props.pokemons.map((item, key) => {
    return <PokemonItem pokemon={item} key={key} />;
  });

  return (
    <>
      <div className="pokemons_title">Pokémons</div>
      <div className="pokemons_list">{items}</div>
    </>
  );
};

export default Pokemons;
