import React from "react";
import { Link } from "react-router-dom";

import "./fichas.scss";

const PlayerCharacters = props => {
  const items = props.characters.map((item, key) => {
    return (
      <Link to={`/fichas/${item.link}`} className="character" key={key}>
        <div className="character_picture">
          <img src={item.picture} />
        </div>

        <div className="character_name">
          <span>{item.photoplayer}</span> as <div>"{item.name}"</div>
        </div>
      </Link>
    );
  });

  return items;
};

const PlayerSections = props => {
  const items = props.players.map((item, key) => {
    return (
      <div className="player_section" key={key}>
        <div className="player_code">
          <span>#{item.code}</span>
        </div>

        <div className="player_characters">
          <PlayerCharacters {...item} />
        </div>
      </div>
    );
  });

  return items;
};

const RegistrosFichas = props => {
  let fichas = require("./fichas.jsx");
  fichas = fichas.default;

  return (
    <div className="fichario">
      <div className="fichario_title">Fichário</div>

      <div className="fichario_list">
        <PlayerSections {...fichas} />
      </div>
    </div>
  );
};

export default RegistrosFichas;
