import React from "react";
import { Route } from "react-router-dom";

import "./registros.scss";

import Indice from "../regras/indice/indice";
import RegistrosFichas from "./fichas/fichas";
import RegistrosGuildas from "./guildas/guildas";

const Registros = ({ match }) => {
  return (
    <>
      <Indice />

      <div className="rules">
        <div className="content">
          <Route path={`${match.url}/fichas`} component={RegistrosFichas} />
          <Route path={`${match.url}/guildas`} component={RegistrosGuildas} />
        </div>
      </div>
    </>
  );
};

export default Registros;
