import React from "react";

import "./character.scss";

const CharacterInfoListItem = props => {
  return (
    <div className="character_info_list_item">
      <div className="character_info_list_item_label">{props.label}</div>
      <div className="character_info_list_item_value">{props.value}</div>
    </div>
  );
};

const CharacterInfo = character => {
  return (
    <div className="character_info">
      <div className="character_info_list">
        <CharacterInfoListItem
          label="Idade"
          value={`${character.idade} anos`}
        />

        <CharacterInfoListItem
          label="Alinhamento"
          value={character.alinhamento}
        />

        <CharacterInfoListItem label="Afinidade" value={character.afinidade} />

        <CharacterInfoListItem
          label="Nível / EXP"
          value={character.nivel + " (" + character.experiencia + "/10)"}
        />

        <CharacterInfoListItem label="Coins (C$)" value={character.coins} />

        <CharacterInfoListItem label="Guilda" value={character.guilda} />

        <CharacterInfoListItem
          label="Player code"
          value={`#${character.player_code}`}
        />

        <CharacterInfoListItem
          label="Última atividade"
          value={character.ultima_atividade}
        />
      </div>

      <div className="character_info_list">
        <CharacterInfoListItem
          label="Vitalidade"
          value={character.atributos.vitalidade}
        />

        <CharacterInfoListItem
          label="Poder"
          value={character.atributos.poder}
        />

        <CharacterInfoListItem
          label="Resistência"
          value={character.atributos.resistencia}
        />

        <CharacterInfoListItem label="Aura" value={character.atributos.aura} />

        <CharacterInfoListItem
          label="Agilidade"
          value={character.atributos.agilidade}
        />
      </div>
    </div>
  );
};

const CharacterPicture = character => {
  return (
    <div className="character_picture">
      <img src={character.foto} alt={character.nome} />
    </div>
  );
};

const Character = props => {
  let character = props.character;

  return (
    <>
      <div className="character_title">
        <div className="character_photoplayer">{character.photoplayer} as</div>
        <div className="character_name">{character.nome}</div>
      </div>

      <div className="top_wrapper">
        <CharacterPicture {...character} />
        <CharacterInfo {...character} />
      </div>
    </>
  );
};

export default Character;
