import React from "react";

import "./vantagens-pericias-missoes.scss";

const VantagensPericiasMissionsItem = props => {
  let descricao = props.descricao.split("\n").join("<br/>");

  return (
    <div className="vantagenspericiasmissions_item">
      <div className="vantagenspericiasmissions_wrapper">
        <div>{props.nome}</div>
        <div>{descricao}</div>
      </div>
    </div>
  );
};

const VantagensPericiasMissionsList = props => {
  let arr = props.vantagens !== undefined ? props.vantagens : props.pericias;

  let items = arr.map((item, key) => {
    return <VantagensPericiasMissionsItem {...item} key={key} />;
  });

  return (
    <div className="vantagenspericiasmissions_list">
      <div className="vantagenspericiasmissions_title">
        {props.vantagens !== undefined ? "Vantagens" : "Perícias"}
      </div>
      {items}
    </div>
  );
};

const MissionItem = props => {
  return (
    <div className="vantagenspericiasmissions_item">
      <div className="vantagenspericiasmissions_wrapper">
        <div className="mission">
          <b>{props.titulo}:</b> {props.status}
        </div>
      </div>
    </div>
  );
};

const MissionsReport = props => {
  let items = props.missoes.map((item, key) => {
    return <MissionItem {...item} key={key} />;
  });

  return (
    <div className="vantagenspericiasmissions_list">
      <div className="vantagenspericiasmissions_title">
        Relatório de missões
      </div>
      {items}
    </div>
  );
};

const VantagensPericiasMissions = props => {
  let vantagens = props.vantagens;
  let pericias = props.pericias;
  let missoes = props.missoes;

  return (
    <div className="vantagenspericiasmissions">
      <VantagensPericiasMissionsList vantagens={vantagens} />
      <VantagensPericiasMissionsList pericias={pericias} />
      <MissionsReport missoes={missoes} />
    </div>
  );
};

export default VantagensPericiasMissions;
