import React from "react";

import "./historia-personalidade.scss";

const HistoriaPersonalidadeItem = props => {
  let text = props.text.split("\n").join("<br/>");

  return (
    <div className="historiapersonalidade_item">
      <div
        className="historiapersonalidade_wrapper"
        dangerouslySetInnerHTML={{ __html: text }}
      />
    </div>
  );
};

const HistoriaPersonalidadeList = props => {
  return (
    <div className="historiapersonalidade_list">
      <div className="historiapersonalidade_title">
        {props.historia !== undefined ? "História" : "Personalidade"}
      </div>
      <HistoriaPersonalidadeItem
        text={
          props.historia !== undefined ? props.historia : props.personalidade
        }
      />
    </div>
  );
};

const HistoriaPersonalidade = props => {
  let historia = props.historia;
  let personalidade = props.personalidade;

  return (
    <div className="historiapersonalidade">
      <HistoriaPersonalidadeList historia={historia} />
      <HistoriaPersonalidadeList personalidade={personalidade} />
    </div>
  );
};

export default HistoriaPersonalidade;
