import React from "react";
import { Link } from "react-router-dom";

import "./guildas.scss";

const GuildSections = props => {
  const items = props.guilds.map((item, key) => {
    return (
      <div className="guild_section" key={key}>
        <div className="guild_picture">
          <img src={item.picture} />
        </div>

        <div className="guild_info">
          <div className="title">{item.name}</div>
          <div>Chefe: {item.topRanking}</div>
          <div>Líder do ranking: {item.topRanking}</div>
          <div>Membros: {item.members}</div>
          <div>Detém as placas: {item.plates.join(", ")}</div>
        </div>
      </div>
    );
  });

  return items;
};

const RegistrosGuildas = props => {
  let guildas = require("./guildas.jsx");
  guildas = guildas.default;

  return (
    <div className="fichario">
      <div className="fichario_title">Guildas</div>

      <div className="fichario_list">
        <GuildSections {...guildas} />
      </div>
    </div>
  );
};

export default RegistrosGuildas;
