import React from "react";

import "./ficha.scss";

import Pokemons from "./pokemons/pokemons";
import Character from "./character/character";
import Itens from "./itens/itens";
import BattleAchievements from "./battle-achievements/battle-achievements";
import VantagensPericiasMissions from "./vantagens-pericias-missoes/vantagens-pericias-missoes";
import HistoriaPersonalidade from "./historia-personalidade/historia-personalidade";
import Reputacoes from "./reputacoes/reputacoes";

const Ficha = props => {
  const { params } = props.match;
  let ficha = require("./json/" + params.uri + ".jsx");
  ficha = ficha.default;

  return (
    <div className="character">
      <div className="character_top">
        <div className="container">
          <Character character={ficha.character} />
        </div>
      </div>

      <div className="container">
        <Pokemons pokemons={ficha.pokemons} />
        <Itens itens={ficha.itens} />

        <VantagensPericiasMissions
          vantagens={ficha.vantagens}
          pericias={ficha.pericias}
          missoes={ficha.missoes}
        />

        <BattleAchievements
          battle={ficha.battle}
          achievements={ficha.achievements}
        />

        <Reputacoes reputacoes={ficha.reputacoes} />

        <HistoriaPersonalidade
          historia={ficha.historia}
          personalidade={ficha.personalidade}
        />
      </div>
    </div>
  );
};

export default Ficha;
