import React from "react";

import "./reputacoes.scss";

const ReputacaoItem = props => {
  return (
    <div className="reputacao_item">
      <div>{props.grupo}</div>
      <div>{props.valor > 0 ? `+${props.valor}` : props.valor}</div>
    </div>
  );
};

const Reputacoes = props => {
  let items = props.reputacoes.map((item, key) => {
    return <ReputacaoItem {...item} key={key} />;
  });

  return (
    <div className="reputacoes">
      <div className="reputacoes_title">Reputações</div>

      <div className="reputacoes_list">{items}</div>
    </div>
  );
};

export default Reputacoes;
