import React from "react";

import "./itens.scss";

const ItensItem = props => {
  let item = props.item;

  let descricao = item.descricao.split("\n").join("<br/>");

  return (
    <div className="itens_item">
      <div className="item_title">
        <div>{item.nome}</div>
        <div>x{item.quantidade}</div>
      </div>

      <div className="item_description">
        <div dangerouslySetInnerHTML={{ __html: descricao }} />
        <div>
          <img src={item.foto} alt={item.nome} />
        </div>
      </div>
    </div>
  );
};

const Itens = props => {
  let items = props.itens.map((item, key) => {
    return <ItensItem item={item} key={key} />;
  });

  return (
    <>
      <div className="itens_title">Inventário</div>

      <div className="itens_list">{items}</div>
    </>
  );
};

export default Itens;
