import React from "react";

import "./regras.scss";

import Indice from "./indice/indice";

const RuleItem = props => {
  let descricao = props.text.split("\n").join("<br/>");

  return (
    <>
      <div className="section_item">
        {props.title && <div className="section_title">{props.title}</div>}
        <div
          className="section_text"
          dangerouslySetInnerHTML={{ __html: descricao }}
        />
      </div>
    </>
  );
};

const RuleSections = props => {
  const items = props.sections.map((item, key) => {
    return <RuleItem {...item} key={key} />;
  });

  return <div className="rule_sections">{items}</div>;
};

const Regras = props => {
  const { params } = props.match;
  let regra = require("./json/" + params.uri + ".jsx");
  regra = regra.default;

  return (
    <>
      <Indice />

      <div className="rules">
        <div className="content">
          <div className="rule_title">{regra.title}</div>
          <RuleSections {...regra} />
        </div>
      </div>
    </>
  );
};

export default Regras;
