import React from "react";

import "./battle-achievements.scss";

const BattleAchievementsItem = props => {
  return (
    <div className="battleachievements_item">
      <div>
        <div>
          <img src={props.foto} alt={props.nome} />
        </div>
      </div>

      <div>{props.nome}</div>
    </div>
  );
};

const BattleAchievementsList = props => {
  let arr = props.battle !== undefined ? props.battle : props.achievements;

  let items = arr.map((item, key) => {
    return <BattleAchievementsItem {...item} key={key} />;
  });

  return (
    <div>
      <div className="battleachievements_title">
        {props.battle !== undefined ? "Battle Box" : "Achievements"}
      </div>

      <div className="battleachievements_list">{items}</div>
    </div>
  );
};

const BattleAchievements = props => {
  let battle = props.battle;
  let achievements = props.achievements;

  return (
    <div className="battleachievements">
      <BattleAchievementsList battle={battle} />
      <BattleAchievementsList achievements={achievements} />
    </div>
  );
};

export default BattleAchievements;
